'use client';

import React, { useState } from 'react';
import { INDIA_MAP_PATHS } from '../constants';

interface IndiaMapProps {
  onStateSelect: (stateName: string) => void;
}

const IndiaMap: React.FC<IndiaMapProps> = ({ onStateSelect }) => {
  const [hoveredState, setHoveredState] = useState<string | null>(null);

  return (
    <div className="flex flex-col items-center justify-center w-full">
      <div className="relative w-full max-w-lg aspect-[3/4] md:aspect-auto md:h-[600px] flex items-center justify-center bg-slate-900 rounded-3xl border border-slate-800 shadow-2xl p-4">
        {/* Background Grid Decoration */}
        <div className="absolute inset-0 bg-[linear-gradient(rgba(255,255,255,0.02)_1px,transparent_1px),linear-gradient(90deg,rgba(255,255,255,0.02)_1px,transparent_1px)] bg-[size:40px_40px] rounded-3xl"></div>
        
        {/* Hover Info Tooltip */}
        <div 
          className={`absolute top-4 right-4 bg-slate-800/95 backdrop-blur border border-emerald-500/30 p-4 rounded-xl shadow-lg z-10 pointer-events-none transition-all duration-300 ${hoveredState ? 'opacity-100 translate-y-0' : 'opacity-0 translate-y-2'}`}
        >
          <h3 className="text-lg font-bold text-white">{hoveredState || 'India'}</h3>
          {hoveredState ? (
             <p className="text-emerald-400 text-xs font-semibold uppercase tracking-wider mt-1">Click to analyze</p>
          ) : (
             <p className="text-slate-500 text-xs mt-1">Select a region</p>
          )}
        </div>

        {/* SVG Map */}
        <svg
          viewBox="0 0 400 460"
          preserveAspectRatio="xMidYMid meet"
          className="w-full h-full drop-shadow-2xl z-0"
          style={{ filter: 'drop-shadow(0 0 20px rgba(16, 185, 129, 0.1))' }}
        >
          <defs>
            <linearGradient id="grad1" x1="0%" y1="0%" x2="100%" y2="100%">
              <stop offset="0%" style={{ stopColor: '#334155', stopOpacity: 1 }} />
              <stop offset="100%" style={{ stopColor: '#1e293b', stopOpacity: 1 }} />
            </linearGradient>
          </defs>

          {INDIA_MAP_PATHS.map((path) => (
            <g key={path.id} onClick={() => onStateSelect(path.name)}>
              <path
                d={path.d}
                className="cursor-pointer transition-all duration-300 ease-out outline-none"
                fill={hoveredState === path.name ? '#10b981' : '#334155'}
                stroke="#475569"
                strokeWidth="1.5"
                onMouseEnter={() => setHoveredState(path.name)}
                onMouseLeave={() => setHoveredState(null)}
                style={{ 
                  opacity: hoveredState && hoveredState !== path.name ? 0.4 : 1,
                  transform: hoveredState === path.name ? 'scale(1.02)' : 'scale(1)',
                  transformBox: 'fill-box',
                  transformOrigin: 'center',
                }}
              />
            </g>
          ))}
        </svg>
      </div>
      <div className="mt-4 text-xs text-slate-500 text-center max-w-md">
        * Interactive stylised map. Boundaries are approximate representation for investment zones.
      </div>
    </div>
  );
};

export default IndiaMap;